////// Packages //////////////////
import React, { FC } from 'react'
import { Label, FormGroup } from 'reactstrap';

////// Local Components //////////

////// Global Components /////////
import Flex from 'components/Flex';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

export const toDoColors = [
    'rgb(235, 235, 200)',
    'rgb(190, 230, 210)',
    'rgb(230, 210, 240)',
    'rgb(170, 200, 230)',
    'rgb(240, 200, 200)'
]

interface Props {
    color?: string;
    setColor: React.Dispatch<React.SetStateAction<string>>
}

const ColorPicker: FC<Props> = ({color, setColor}) => (
    <FormGroup>
        <Label for="colors">Color</Label>
        <Flex justify="between">
            {toDoColors.map( c => (
                <div 
                    key={c}
                    className={`cursor-pointer border ${color === c ? 'border-2x border-500' : ''}`} 
                    style={{backgroundColor: c, width: "2rem", height: "2rem"}} 
                    onClick={()=>setColor(c)}
                />
            ))}
        </Flex>
    </FormGroup>
)

export default ColorPicker